<template>
  <div class="row list prd-list mb-lg-4 mx-n2">
    <div
      class="col-lg-4 col-sm-6 mb-4 pb-3 px-2"
      v-for="item in list[page - 1]"
      :key="item"
    >
      <div class="inner bg-white p-4">
        <div class="img mb-3 position-relative">
          <span class="tag">熱賣商品</span>
          <router-link :to="`/product/${item.id}`" @click="$emit('prd-link', item.id)">
            <img :src="item.imageUrl" :alt="item.title" class="w-100" />
          </router-link>
        </div>
        <router-link
          :to="`/product/${item.id}`"
          class="d-inline-block title mb-lg-4"
          @click="$emit('prd-link', item.id)"
        >
          {{ item.title }}
        </router-link>
        <div
          class="mb-lg-5 mb-4 row mx-0 align-items-center justify-content-between pt-2 flex-sm-nowrap"
        >
          <a href="" class="me-3 cart_img col-auto px-0" @click.prevent="addCart(item)">
            <img src="./img/cart.svg" alt="" />
          </a>
          <span
            class="col text-end px-0 d-flex flex-wrap justify-content-end align-items-end"
          >
            <span class="old-price mb-1">原價 $ 150</span>
            <span class="ms-md-3 price mb-1">限量優惠 <span>$ 120</span></span>
          </span>
        </div>
        <div class="text-center mb-lg-3 mb-2">
          <router-link
            :to="`/product/${item.id}`"
            class="btn btn-outline-primary"
            @click="$emit('prd-link', item.id)"
          >
            詳細介紹
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: [
    'list',
    'page'
  ],
  inject: ['emitter'],
  methods: {
    addCart (item) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      const cart = {
        product_id: item.id,
        qty: 1
      }
      this.$http.post(api, { data: cart })
        .then((res) => {
          if (res.data.success) {
            this.emitter.emit('getData')
            this.$httpMessage(res, '加入購物車')
          }
        })
    }
  }
}
</script>
