<template>
  <loading :active="isLoading"></loading>
  <pagesBanner></pagesBanner>
  <section class="product_list pb-5" ref="product_list">
    <div class="container position-relative z-index-1">
      <div class="px-lg-5">
        <div class="bread-crumbs pt-2 pb-4">
          <ol itemscope="" itemtype="http://schema.org/breadcrumbList" id="breadcrumbs">
            <li itemprop="itemListElement" itemscope="" itemtype="http://schema.org/ListItem">
              <router-link to="/index" itemprop="item" title="劉記花生">
              <span itemprop="name">首頁</span>
            </router-link>
              <meta itemprop="position" content="1">
            </li>
            <li itemprop="itemListElement" itemscope="" itemtype="http://schema.org/ListItem">
              <span class="mx-2">/</span>
              <router-link to="/products" itemprop="item" title="商品介紹">
                <span itemprop="name">商品介紹</span>
              </router-link>
              <meta itemprop="position" content="2">
            </li>
            <li itemprop="itemListElement" itemscope="" itemtype="http://schema.org/ListItem"
              v-if="$route.params.categoryName">
              <span class="mx-2">/</span>
              <router-link :to="`/products/${$route.params.categoryName}`" itemprop="item" :title="$route.params.categoryName">
                <span itemprop="name">{{ $route.params.categoryName }}</span>
              </router-link>
              <meta itemprop="position" content="3">
            </li>
          </ol>
        </div>
        <ul class="nav nav-pills mb-4 mx-n2 justify-content-center justify-content-lg-start" id="pills-tab" role="tablist">
          <li class="nav-item" role="presentation">
            <router-link :to="`/products`" class="nav-link" :class="{ active: $route.params.categoryName === '' }">
              全部商品
            </router-link>
          </li>
          <li class="nav-item" role="presentation" v-for="item in classification" :key="item">
            <router-link :to="`/products/${item}`" class="nav-link" :class="{ active: $route.params.categoryName === item }">
              {{ item }}
            </router-link>
          </li>
        </ul>
        <div class="tab-content mb-4" id="pills-tabContent">
          <div class="tab-pane fade show active" role="tabpanel">
            <ProductList :list="filterData" :page="currentPage"></ProductList>
          </div>
        </div>
        <Pagination :pages="filterData" :page="currentPage" @chang-page="changPage"></Pagination>
      </div>
    </div>
  </section>
</template>

<script>
import Pagination from '../components/UserPagination.vue'
import pagesBanner from '../components/pagesBanner.vue'
import ProductList from '../components/ProductList.vue'
export default {
  components: {
    pagesBanner,
    ProductList,
    Pagination
  },
  data () {
    return {
      isLoading: false,
      products: [],
      classification: [],
      currentPage: 1
    }
  },
  watch: {
    filterData () {
      this.currentPage = 1
    }
  },
  computed: {
    filterData () {
      const vm = this
      if (this.$route.params.categoryName === '') {
        const newRecords = []
        this.products.forEach((item, i) => {
          if (i % 6 === 0) {
            newRecords.push([])
          }
          const page = parseInt(i / 6)
          newRecords[page].push(item)
        })
        return newRecords
      } else {
        var newProducts = []
        this.products.forEach(function (item) {
          if (item.category === vm.$route.params.categoryName) {
            newProducts.push(item)
          }
        })
        const newRecords = []
        newProducts.forEach((item, i) => {
          if (i % 6 === 0) {
            newRecords.push([])
          }
          const page = parseInt(i / 6)
          newRecords[page].push(item)
        })
        return newRecords
      }
    }
  },
  mounted () {
    this.getProducts()
  },
  methods: {
    getProducts () {
      this.isLoading = true
      const vm = this
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      this.$http.get(api)
        .then((res) => {
          this.isLoading = false
          if (res.data.success) {
            this.products = res.data.products
            if (vm.classification.length === 0) {
              let set = new Set()
              set = this.products.filter(function (item) {
                if (!set.has(item.category)) {
                  return set.add(item.category)
                }
              })
              set.filter(function (item) {
                return vm.classification.push(item.category)
              })
            }
          }
        })
        .then(() => {
          vm.gsap.from('.prd-list > div', {
            scrollTrigger: '.prd-list',
            duration: 1.15,
            y: 50,
            opacity: 0,
            stagger: 0.3
          })
        })
    },
    changPage (page) {
      this.currentPage = page
      document.documentElement.scrollTop = this.$refs.product_list.offsetTop - 150
    }
  }
}
</script>
