<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination justify-content-center">
      <li class="page-item" :class="{'disabled' : page == 1}">
        <a class="page-link" href="#" aria-label="Previous" @click.prevent="this.$emit('chang-page', page - 1)">
          <font-awesome-icon icon="fa-solid fa-chevron-left" />
        </a>
      </li>
      <li class="page-item" v-for="item in pages.length" :key="item">
        <a class="page-link" href="#"
          :class="{'active' : page === item}"
          @click.prevent="this.$emit('chang-page', item)">{{ item }}</a>
      </li>
      <li class="page-item" :class="{'disabled' : page == pages.length}">
        <a class="page-link" href="#" aria-label="Next" @click.prevent="this.$emit('chang-page', page + 1)">
          <font-awesome-icon icon="fa-solid fa-chevron-right" />
        </a>
      </li>
    </ul>
  </nav>
</template>
<script>
export default {
  props: [
    'pages',
    'page'
  ]
}
</script>
